import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";

import {Layout} from "./hocs/Layout";
import {Home} from "./containers/Home";
import {Login} from "./containers/Login";
import {Signup} from "./containers/SignUp";
import {ResetPassword} from './containers/ResetPassword'
import {API, setCallbackFor401} from "./api/api";
import {Logout} from "./containers/Logout";
import {Registration} from "./containers/Registration";


const home = "/";
const login = "/login";
const logout = "/logout";
const registration = "/registration";
const signup = "/signup";
const reset_password = "/reset-password";


export const App = () => {

    const [userName, setUser] = useState("");

    const getUser = async () => {
        let user = await API.me();
        if (user) {
            let username = user['username'];
            setUser(username)
        }
    }


    useEffect(async () => {
        getUser();
    }, []);

    const notauthorized = () => {
        setUser('');
    }
    setCallbackFor401(notauthorized);

    return (
        <Router>
            <Layout userName={userName} notauthorized={notauthorized}>
                <Switch>
                    <Route exact path={home}
                           component={Home}/>
                    <Route exact path={login}
                           component={userName ? () => <Redirect to={home}/> : () => <Login getUser={getUser} />}/>
                    <Route exact path={logout}
                           component={!userName ? () => <Redirect to={login}/> : Logout}/>
                    <Route exact path={registration}
                           component={userName ? <Redirect to={home}/> : () => <Registration/>}/>
                    <Route exact path={signup}
                           component={userName ? <Redirect to={home}/> : () => <Signup/>}/>
                    <Route exact path={reset_password}
                           component={!userName ? <Redirect to={login}/> : () => <ResetPassword/>}/>
                    <Route path={"*"} component={NotFound}/>
                </Switch>
            </Layout>
        </Router>
    );
}


const NotFound = () => {
    return <div>404</div>
}
